.gpt_features-container-feature {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    margin-bottom: 2rem;
}

.gpt_features-container-feature-title {
    flex: 1;
    /* max-width: 180px; */
    margin-right: 2rem;
    
}

.gpt_features-container-feature-title h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 24px;
    line-height: 24px;
    /* letter-spacing: -0.04em; */
    color: #fff;
}

.gpt_features-container-feature-title div {
    width: 38px;
    height: 3px;
    background: var(--gradient-bar);
    box-shadow: 0 4px 4px rgba(0,0,0,0.5);
    margin-bottom: 0.35rem;
}

.gpt_features-container-feature-text {
    flex: 3;
    /* max-width: 390px; */
    display: flex;
}

.gpt_features-container-feature-text p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    /* letter-spacing: -0.04em; */
    color: var(--color-text);
}

@media screen and (max-width: 550px) {
    .gpt_features-container-feature-title h1 {
        font-size: 14px;
        line-height: 22px;
    }

    .gpt_features-container-feature-title p {
        font-size: 12px;
        line-height: 20px;
    }

    .gpt_features-container-feature {
        margin: 1rem 0 ;
    }
}