.gpt_header {
    display: flex;
}

.gpt_header-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-right: 5rem;
}

.gpt_header-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;
    
}

.gpt_header-content p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    color: var(--color-text);
    margin-top: 1.5rem;

}

.gpt_header-content-input {
    width: 100%;
    margin: 2rem 0 1rem;
    display: flex;
}

.gpt_header-content-input input {
    flex: 2;
    width: 100%;
    background: var(--color-footer);
    border-radius: 5px 0px 0px 5px;
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    border: 2px solid var(--color-footer);
    padding: 0 1rem;
    outline: none;
    color: #fff;
    min-height: 50px;

}

.gpt_header-content-input button {
    flex: 0.6;
    width: 100%;
    min-height: 50%;
    background: #FF4820;
    border-radius: 0px 5px 5px 0px;
    font-family: var(--font-family);
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 27px;
    color: #FFFFFF;
    border: none;
    outline: none;
    cursor: pointer;
    padding: 0 1rem;
}

.gpt_header-content-people {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    margin-top: 2rem;
    align-items: center;
}

.gpt_header-content-people img {
    width: 181.79px;
    height: 38px;
}

.gpt_header-content-people p {
    margin: 0 0 0 1rem;
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    color: #FFFFFF;
    text-align: center;
}

.gpt_header-image {
    flex: 1;
    display: flex;
    justify-items: center;
    align-items: center;

}

.gpt_header-image img {
    width: 100%;
    height: 100%;
}

@media screen and (max-width: 1500px) {
    .gpt_header h1 {
        font-size: 48px;
        line-height: 60px;
    }
}

@media screen and (max-width: 1300px) {
    .gpt_header h1 {
        font-size: 44px;
        line-height: 54px;
    }

    .gpt_header p {
        font-size: 18px;
        line-height: 24px;
    }
}

@media screen and (max-width: 1050px) {
    .gpt_header {
        flex-direction: column;
    }

    .gpt_header-content {
        margin: 0 0 3rem;
    }


}

@media screen and (max-width: 650px) {
    .gpt_header h1 {
        font-size: 48px;
        line-height: 60px;
    }

    .gpt_header p {
        font-size: 16px;
        line-height: 24px;
    }

    .gpt_header-content-people {
        flex-direction: column;
    }

    .gpt_header-content-people p {
        margin: 0;
    }

    .gpt_header-content-input input, 
    .gpt_header-content-input button  {
        font-size: 16px;
        line-height: 24px;
    }

}

@media screen and (max-width: 490px) {
    .gpt_header h1 {
        font-size: 30px;
        line-height: 48px;
    }

    .gpt_header p {
        font-size: 14px;
        line-height: 24px;
    }

    .gpt_header-content-input input, 
    .gpt_header-content-input button  {
        font-size: 12px;
        line-height: 16px;
    }
}




